import React, { Component } from "react";
import NavBarStatement from '../statement/statementNavbar';
class Betting extends Component {
  state = {
    tableHead: [
      { id: "1", header: "Match" },
      { id: "3", header: "Date" },
      { id: "4", header: "Question" },
      { id: "5", header: "Choiced" },
      { id: "6", header: "Amount" },
      { id: "7", header: "Win/loos" },
    ],
    tableData: [
      {
        id: "1",
        date:"1/2/3",
        choiced: "Bn",
        amount: "12",
        win: "true",
        match: "BN vs IN",
        question: "To win the match",
      }
    ],
  };
  render() {
   
    return (
      <div>
        <NavBarStatement />
        <table className="table table-dark table-striped ">
          <thead>
            <tr>
              {this.state.tableHead.map(({ id, header }) => {
                return (
                  <th scope="col" key={id}>
                    {header}
                  </th>
                );
              })}
            </tr>
          </thead>
          <tbody>
            {this.state.tableData.map(
              ({ question, choiced, amount, win, match ,date}) => {
                return (
                  <tr>
                    <td>{match}</td>
                    <td>{date}</td>
                    <td>{question}</td>
                    <td>{choiced}</td>
                    <td>{amount}</td>
                    <td className="green">{win}</td>
                  </tr>
                );
              }
            )}
          </tbody>
        </table>
      </div>
    );
  }
}

export default Betting;
